/* âœ… Full Page Container - Pushed content lower */
.full-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start; /* Ensure content starts from the top */
    min-height: 100vh;
    background-color: #f7f7f7;
    padding: 20px;
    box-sizing: border-box;
    text-align: center;
    padding-top: 120px; /* Increased padding to move content down */
  }
  
  /* âœ… Card Alignment */
  .card {
    background: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 500px;
    margin-bottom: 30px; /* Space below the card */
  }
  
  /* âœ… Lottie Animation - Increased Size & Centered */
  .lottie-container {
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .lottie-animation {
    width: 280px;  /* Increased size for better visibility */
    height: 280px; /* Adjusted height to maintain aspect ratio */
    max-width: 350px;
    max-height: 350px;
  }
  
  /* âœ… Cat Quote - Positioned Below the Animation */
  .cat-quote {
    font-size: 18px;
    font-style: italic;
    color: #333;
    text-align: center;
    width: 80%; /* Keeps it responsive */
    margin-top: 20px; /* Adds space below animation */
  }
  
  /* âœ… Modal Overlay */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6); /* Dark background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
  }
  
  /* âœ… Modal Content */
  .modal-content {
    background: white;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    max-width: 400px;
    width: 90%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
  
  .modal-content h3 {
    margin-bottom: 20px;
    font-size: 22px;
    font-weight: bold;
  }
  
  /* âœ… Button Container - Side-by-Side */
  .modal-content .button-container {
    display: flex;
    justify-content: space-between; /* Align buttons side-by-side */
    gap: 20px; /* Add space between buttons */
    margin-top: 20px;
  }
  
  /* âœ… Button Styling */
  .modal-content button {
    padding: 12px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 48%; /* Ensures buttons are side-by-side with equal width */
  }
  
  .modal-content button:hover {
    opacity: 0.8;
    transform: scale(1.05); /* Slight zoom on hover */
  }
  
  .modal-content .cancel {
    background-color: #aaa;
    color: white;
  }
  
  .modal-content button:active {
    transform: scale(0.98); /* Slight shrink on click */
  }
  
  /* âœ… Button Styling */
  .button {
    background-color: #000;
    color: white;
    padding: 12px 20px;
    font-size: 16px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .button:hover {
    opacity: 0.8;
    transform: scale(1.05);
  }
  
  .success-message {
    font-size: 18px;
    font-weight: bold;
    color: green;
    margin-top: 20px;
  }
  