/* âœ… Full Page Container - Moves Content Lower */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-height: 100vh;
    background-color: #f7f7f7;
    padding: 20px;
    box-sizing: border-box;
    text-align: center;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    padding-top: 80px; /* Increased to push content lower */
  }
  
  /* âœ… Header Section */
  .header {
    text-align: center;
    margin-bottom: 25px;
  }
  
  .header h2 {
    font-size: 24px; /* Font size for title */
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .sub-text {
    font-size: 16px; /* Slightly smaller subtext */
    color: #555;
  }
  
  /* âœ… Main Card Styling */
  .card {
    background: white;
    padding: 15px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 400px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .card h3 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .card p {
    font-size: 14px;
    color: #666;
    margin-bottom: 15px;
  }
  
  .card .button {
    width: 100%;
    padding: 12px;
    font-size: 16px;
    background: black;
    color: white;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .card .button:hover {
    background: #333;
  }
  
  /* âœ… Recent Transactions Section */
  .sub-heading {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
  }
  
  .transaction-card {
    background: white;
    padding: 15px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .transaction-details {
    flex-grow: 1;
    text-align: left;
  }
  
  .transaction-name {
    font-size: 16px;
    font-weight: bold;
    color: #333;
  }
  
  .transaction-amount {
    font-size: 14px;
    color: #666;
  }
  
  .transaction-time {
    font-size: 12px;
    color: #888;
  }
  
  /* âœ… Adjust View Button */
  .transaction-card .button.small {
    font-size: 14px;
    padding: 12px 48px;
    background: black;
    color: white;
    border-radius: 8px;
    cursor: pointer;
    width: auto; /* Auto size for button */
    margin-left: 20px; /* Space between transaction info and button */
    text-align: center;
  }
  
  .transaction-card .button.small:hover {
    background: #333;
  }
  
  /* âœ… No Transactions Message */
  .no-transactions {
    font-size: 16px;
    color: #888;
    text-align: center;
  }
  