/* âœ… General Navbar Styling */
.navbar {
    display: flex;
    justify-content: space-between; /* âœ… Proper spacing */
    align-items: center;
    padding: 15px 20px;
    background: #000;
    color: white;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000; /* âœ… Ensures it's always on top */
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  /* âœ… Navbar Brand */
  .logo {
    font-size: 22px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
  }
  
  /* âœ… Mobile Navbar Hamburger Menu (Only shown if it's not the Payment Page) */
  .hamburger {
    display: none;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
  }
  
  .bar {
    width: 30px;
    height: 3px;
    background: white;
    transition: 0.4s;
  }
  
  /* âœ… Mobile Responsive Navbar */
  @media (max-width: 768px) {
    .hamburger {
      display: flex;
    }
  
    .nav-links {
      position: absolute;
      top: 60px;
      right: 0;
      background: #000;
      width: 100%;
      flex-direction: column;
      gap: 10px;
      padding: 20px;
      text-align: center;
      transform: translateY(-100%);
      transition: transform 0.4s ease-in-out;
      display: none; /* âœ… Initially Hidden */
    }
  
    .nav-links.active {
      transform: translateY(0);
      display: flex; /* âœ… Show menu when active */
    }
  
    /* âœ… Hamburger Animation */
    .hamburger.open .bar:nth-child(1) {
      transform: rotate(45deg) translateY(8px);
    }
  
    .hamburger.open .bar:nth-child(2) {
      opacity: 0;
    }
  
    .hamburger.open .bar:nth-child(3) {
      transform: rotate(-45deg) translateY(-8px);
    }
  }
  