.split-form {
    width: 90%;
    max-width: 400px;
    padding: 20px;
    border-radius: 12px;
    background: white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .split-form label {
    font-weight: bold;
    display: block;
    margin-top: 10px;
    text-align: left;
  }
  
  .split-form input {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 16px;
  }
  
  .split-form .button {
    width: 100%;
    margin-top: 15px;
    padding: 12px;
    font-size: 18px;
    background: black;
    color: white;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .split-form .button:hover {
    background: #333;
  }
  