@tailwind base;
@tailwind components;
@tailwind utilities;

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh; /* âœ… Stretches the page */
  background-color: #f7f7f7;
  padding: 20px;
}

.card {
  background: white;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 90%;
  max-width: 400px; /* âœ… Keeps things neat */
  margin-bottom: 20px;
}

/* âœ… Navbar Fix */
.navbar {
  width: 100%;
  padding: 15px;
  background: #000;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* âœ… Buttons */
.button {
  background: black;
  color: white;
  padding: 12px 16px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.button.small {
  font-size: 14px;
  padding: 8px 12px;
}

/* âœ… Spacing & Text Styling */
.header {
  text-align: center;
  margin-bottom: 20px;
}

.sub-heading {
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
}

/* âœ… Transaction Cards */
.transaction-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

/* âœ… Payment Links */
.participant {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.full-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f7f7f7;
  padding: 20px;
  box-sizing: border-box; /* Ensure padding doesn't affect width/height */
}

