/* âœ… Full Page Container - Moves Content Lower */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  background-color: #f7f7f7;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  padding-top: 90px; /* ðŸ”¥ Increased to push content lower */
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

/* âœ… Header - More Spacing Below */
.header {
  text-align: center;
  margin-bottom: 25px; /* Increased space below header */
}

.title {
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 15px; /* Extra space below title */
}

.total-amount {
  font-size: 20px;
  color: #333;
  margin-bottom: 20px; /* More spacing below total amount */
}

/* âœ… UPI Card */
.upi-card {
  width: 90%;
  max-width: 420px;
  padding: 20px;
  border-radius: 12px;
  background: white;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 35px; /* ðŸ”¥ More space below UPI card */
}

/* âœ… Participants Section */
.sub-heading {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-top: 40px; /* ðŸ”¥ More space between UPI and Participants */
  color: #333;
}

.participants-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 420px;
  margin-top: 30px; /* ðŸ”¥ More space above participant cards */
  align-items: center;
}

/* âœ… Participant Cards */
.participant {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  width: 51%;
  margin-bottom: 20px; /* ðŸ”¥ More space between participant cards */
  max-width: 420px;
}

/* âœ… Adjust Copy Link Button */
.button.small {
  font-size: 16px;
  padding: 10px 15px;
  background: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  margin-left: 15px;
}

.button.small:hover {
  background: #222;
}
